
export class ManageMaster {

    static types : any = null;
    static categories : any;
    static subCategories : any;

    static typeMap : Map<number, any> = new Map<number, any>();
    static categoryMap = new Map<number, any>();

    static initType(type:number)
    {
        if(type === 1){
            this.typeMap.set(1,
                [
                    {num:12,label:"所得"},
                    {num:13,label:"資産・引出金"},
                    {num:14,label:"借金"},
                ]
            );
        } else if(type === 2)
        {
            this.typeMap.set(2,
                [
                    {num:1,label:"食費"},
                    {num:2,label:"住居費"},
                    {num:3,label:"水道光熱費"},
                    {num:4,label:"教育費"},
                    {num:5,label:"教養・娯楽費"},
                    {num:6,label:"交通・通信費"},
                    {num:7,label:"医療・衛生費"},
                    {num:8,label:"交際費"},
                    {num:9,label:"車両費"},
                    {num:10,label:"税金・社会保険料"},
                    {num:11,label:"特別費"},
                ]
            );
        }
    }

    static getTypes():any
    {
        return [
            {num:1,label:"収入"},
            {num:2,label:"支出"}
          ];
    }

    static getCategories(type:number):any
    {
        if(this.typeMap.has(type) === false)
        {
            this.initType(type);
        }
        
        return this.typeMap.get(type);
    }
}


