import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Component, OnInit ,ChangeDetectorRef} from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { CalendarEditComponent} from '../calendar-edit/calendar-edit.component';
//Todo.tsを読み込む
import { ModelEvent } from '../models/modelEvent';
import { EditInfo } from '../models/editInfo';

//定義型読み込み
import {ImportanceType} from '../common/enumDefine';

import { ManageMaster } from '../manage/manageMaster';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.css']
})

export class CalendarComponent implements OnInit {

  ngOnInit() {
  }

  firstWeek:Date[];
  secondWeek:Date[];
  thirdWeek:Date[];
  forthWeek:Date[];
  fifthWeek:Date[];

  targetMouthEvents:ModelEvent[];

  targetEvent:ModelEvent;

  //impTypes:any;
  
  targetYear:number;
  targetMouth:number;

  constructor(private chRef: ChangeDetectorRef
    ,private modalService: NgbModal
    ,private http: HttpClient){
    var today = new Date();

    this.targetEvent = new ModelEvent();

    // this.impTypes = [
    //   {num:ImportanceType.Normal,label:"通常"},
    //   {num:ImportanceType.High,label:"高"}, 
    //   {num:ImportanceType.Low,label:"低"}, 
    //   {num:ImportanceType.Week,label:"隔週"}, 
    //   {num:ImportanceType.Month,label:"隔月"},
    // ];

    this.createCalendar(today.getFullYear(),today.getMonth() + 1);
  }
  
  onClickEvent(event:ModelEvent){
    this.targetEvent = event;
    this.chRef.detectChanges();
  }

  //追加登録ボタン押下
  onClickRegisterOpen()
  {
    var ei = new EditInfo();
    ei.editMode = 1;
    
    const modalRef = this.modalService.open(CalendarEditComponent);
    modalRef.componentInstance.editTarget = new ModelEvent();
    modalRef.componentInstance.editInfo = ei;
    modalRef.result.then(
      result => {
        if(result !== null){
          this.targetMouthEvents[result.id] = result;
        }
      }
    );
  }

  //変更ボタン押下
  onClickEditOpen()
  {
    var ei = new EditInfo();
    ei.editMode = 2;
    
    const modalRef = this.modalService.open(CalendarEditComponent);
    modalRef.componentInstance.editTarget = this.targetEvent.clone();
    modalRef.componentInstance.editInfo = ei;
    modalRef.result.then(
      result => {
        if(result !== null){
          this.targetMouthEvents[result.id].reflect(result);
        }
      }
    );
  }

  //年月日の変更イベント
  onClickChangeMonth(year:number,month:number,monthDiff:number){
  
    month = month + monthDiff;
    //年月の更新
    if(month === 0){
      year = year -1;
      month = 12;
    }else if(month === 13){
      year = year + 1;
      month = 1;
    }
  
    //カレンダーの更新
    this.createCalendar(year,month);
  }

  //カレンダー作成
  createCalendar(targetY:number,targetM:number){
    
    this.targetYear = targetY;
    this.targetMouth = targetM;

    //今月のイベントを取得
    this.targetMouthEvents = new Array();
    ///////////////////////////////////
    this.http.get<ModelEvent[]>('api/getMonthEvents')
    // subscribeの時点でModelEvent[]として受け取れる
    .subscribe(response => {
        response.forEach(function( value ) {
          this.targetMouthEvents[value.id] = value;
        });
    });
    ////////////////////////////////////////////////////////////////////////

    var todo = new ModelEvent();
    todo.id = 100;
    todo.type = 2;
    todo.category = 3;
    todo.subCategory = 1;
    todo.detail = "testes";
    todo.registerDate = new Date(2018,3,1);
    this.targetMouthEvents[todo.id] = todo;

    todo = new ModelEvent();
    todo.id = 101;
    todo.type = 2;
    todo.category = 4;
    todo.subCategory = 2;
    todo.detail = "testes2";
    todo.registerDate = new Date(2018,3,3);
    this.targetMouthEvents[todo.id] = todo;

    ////////////////////////////////////////////////////////////////////////

    //対象年月の１日を取得
    var target = new Date(targetY,targetM,1);
    
    //1日の曜日を取得
    var firstDayNumber = target.getDay();
    
    //一週目の日曜の日付を計算
    var startDay = this.addDate(target,-firstDayNumber);
    
    //５週目までの日付をすべて埋める
    this.firstWeek = new Array();
    startDay = this.setWeekDays(this.firstWeek,startDay);
    this.secondWeek = new Array();
    startDay = this.setWeekDays(this.secondWeek,startDay);
    this.thirdWeek = new Array();
    startDay = this.setWeekDays(this.thirdWeek,startDay);
    this.forthWeek = new Array();
    startDay = this.setWeekDays(this.forthWeek,startDay);
    this.fifthWeek = new Array();
    startDay = this.setWeekDays(this.fifthWeek,startDay);

  }

  //対象日付から始まる１周間を格納
  setWeekDays(week:Date[],startDay:Date):Date{

    for(let i = 0; i < 7; i++){
      week.push(startDay);
      startDay = this.addDate(startDay,1);
    }

    return startDay;
  }

  //対象日付のTodoを取得する
  getTodoForDate(target:Date){
    
  }

  //対象日付に番号分たされた年月日を返す
  addDate(target:Date,addNumber:number):Date{

    var newDate = new Date(target.getFullYear(),target.getMonth(),target.getDate());
    newDate.setDate(newDate.getDate() + addNumber);
    
    return newDate;
  }

  getDayEvent(target:Date):ModelEvent[]{

    var ret = new Array();

    this.targetMouthEvents.forEach(function( value ) {
      if(target.getFullYear() === value.registerDate.getFullYear()
        && target.getMonth() === value.registerDate.getMonth()
        && target.getDate() === value.registerDate.getDate())
      {
        ret.push(value);
      }
    });

    return ret;
  }
  
  getDisplay(model:ModelEvent):string
  {
      var found = ManageMaster.getCategories(model.type).find(function(target) {
          return target.num === model.category;
        });

      return found.label;
  }

  //土曜かどうか
  is_saturday(target:Date):boolean{

    //対象の曜日を取得
    var dayNumber = target.getDay();

    return dayNumber === 6;
  }

  //日曜かどうか
  is_sunday(target:Date):boolean{
    
    //対象の曜日を取得
    var dayNumber = target.getDay();
    
    return dayNumber === 0;
  }

  //祝日かどうか
  is_holiday(target:Date):boolean{
    
    //対象の曜日を取得
    var dayNumber = target.getDay();
    
    return dayNumber === 0;
  }
  getCalClass(model:ModelEvent):string
  {
      let cls :string;
      switch(model.type)
      {
        case 1:
          cls = "income";
          break;
        case 2:
          cls = "expence";
          break;
      }

      return cls;
  }
}



